.categories-list {
  list-style: $list-style;
  margin: 0;
  padding: 0;
}

.categories-list__item {
  background-color: $accent-color-main;
}

.categories-list__second-item {
  background-color: $accent-color-second;
}

.categories-list__link {
  color: $accent-text-color;
}

@media (min-width: $tablet-full-width) {
  .categories-list {
    display: flex;
  }

  .categories-list__item {
    width: 50%;
  }
}
